.button {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid #ccc;
    color: #000;
    margin-right: 15px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
}
.button:focus {
    outline: none;
}
.button:active {
    box-shadow: inset 2px 2px 1px rgba(0, 0, 0, .3);
}
.button:disabled {
    background: #ccc;
    color: #000;
    cursor: not-allowed;
}
.button.success {
    background: rgba(161, 240, 69, 1);
}

.button.error {
    background: rgba(240, 87, 108, 1);
}
.primary {
    background: #24498A;
    color: #fff;
}